import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { InfoCard } from 'components/common';
import { getPolicyObjectNo, getSubsidizeObjectNo, getPrioritizeObjectNo } from 'services/quicklink';
import ComeFromIcon from 'components/icons/ComeFromIcon';
import routeConstant from 'routes/route/routeConstant';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'ĐT chính sách',
    service: getPolicyObjectNo,
    url: routeConstant.DANH_MUC__DOI_TUONG__CHINH_SACH,
    icon: ComeFromIcon,
  },
  {
    title: 'ĐT trợ cấp',
    service: getSubsidizeObjectNo,
    url: routeConstant.DANH_MUC__DOI_TUONG__TRO_CAP,
    icon: ComeFromIcon,
  },
  {
    title: 'ĐT ưu tiên',
    service: getPrioritizeObjectNo,
    url: routeConstant.DANH_MUC__DOI_TUONG__UU_TIEN,
    icon: ComeFromIcon,
  },
];

class DashboardCategory extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardCategory {...props} />
  </Page>
);



